










import Vue from 'vue'
import Container from './container'
import Wrapper from './wrapper'

export default Vue.extend({
  name: 'DeleteEvent',
  components: {
    Container,
    Wrapper,
  },
  props: {
    event: Object,
  },
  computed: {
    description() {
      const event = this.event as any
      return `delete ${event.payload.ref_type}: ${event.payload.ref}`
    }
  },
})
